const swiperBanner = new Swiper(".swiperBanner", {
    slidesPerView: 1,
    direction: 'horizontal',
    pagination: {
        el: ".swiper-pagination",
        clickable: true,

    },
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
    },
    breakpoints: {
        767.98: {
            direction: "vertical",
            slidesPerView: 1,
        }
    }

});
const swiper = new Swiper(".swiperBeauty", {
    slidesPerView: 1,
    pagination: {
        el: ".swiper-pagination",
        clickable: true,
        dynamicBullets: true,
    },
    navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
    },
    breakpoints: {
        767.98: {
            pagination: {
                dynamicBullets: false,
            }
        }
    }
});
